.work {
  background-color: var(--first-bkg-color);
  padding: 100px 0;
  &__title {
    text-align: center;
  }
  &__video {
    margin-top: 57px;
    position: relative;
    transition: filter .5s ease-in;
    &:hover {
      filter: grayscale(100%);
    }
    &-button {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: var(--light-color);
      width: 100px;
      height: 100px;
      border-radius: 50%;
      img {
        width: 80%;
        top: 50%;
        left: 50%;
        position: absolute;
        transform: translate(-45%, -50%);
      }
    }
  }
}
@media (max-width: 1200px) {
  .work {
    padding: 50px 0;
  }
}

@media (max-width: 992px) {

}

@media (max-width: 768px) {

}

@media (max-width: 576px) {
  .work {
    &__video {
      margin-top: 30px;
      &-button {
        width: 50px;
        height: 50px;
      }
    }
  }
}


